.toms-video-player-panel-body{
    border-top: 1px solid #ccc !important;
    background-color: rgba(172,247,162,0.32);
    
    h2{
        &:hover{
           background-color: #acf7a2 !important;
        }
        button{
            &:focus{
                box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) #2196f3 !important;
            }
        }
    }
}

.toms-video-player-content{
    width: 100%;
    height: auto;
    margin: 0 5px 5px 5px;

    .toms-video-player-default-image{
        text-align: center;
    }
}

/* Video Url */
.toms-video-player-url{
    display: flex;

    label{
        font-weight: bold;
    }
    .toms-video-player-video-main-url{
        flex: 1;
        margin-bottom: 0px !important;
    }
}
.toms-video-player-main-media-botton{
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .toms-video-player-main-media-botton-remove{
        margin: 8px;
        .toms-video-player-main-media-botton-remove-icon{
            color: #c50f0f;
            transition: transform 0.3s ease-out;
    
            &:hover{
                color: #ff0000;
                transform: scale(1.25) rotate(180deg);
            }
        }
    }
}
.toms-video-player-main-options{
    display: flex;

    .toms-video-player-main-type{
        flex: 1;
        margin: 0 1px 0 0;
    }
    .toms-video-player-width{
        flex: 1;
        margin: 0 1px 0 1px;
    }
    .toms-video-player-height{
        flex: 1;
        margin: 0 0 0 1px;
        margin-bottom: 0 !important;
    }
}

/* Poster image */
.toms-video-player-poster-lock{
    margin-top: 10px;

    p{
        margin-bottom: 0;
    }
}
.toms-video-player-poster{
    display: flex;
    margin-top: 0;
    
    label{
        font-weight: bold;
    }
    .toms-video-player-poster-url{
        flex: 1;
        margin-bottom: 0 !important;
    }
}
.toms-video-player-poster-media{
    display: flex;
    justify-content: flex-end;

    .toms-video-player-poster-media-button-remove{
        margin: 8px;

        .toms-video-player-poster-media-button-remove-icon{
            color: #c50f0f;
            transition: transform 0.3s ease-out;
    
            &:hover{
                color: #ff0000;
                transform: scale(1.25) rotate(180deg);
            }
        }
    }
}